import { memo, useState } from "react";
import { Link } from "react-router-dom";
import { Segment, Header } from "semantic-ui-react";
import styled from "styled-components";
import { NoSelect } from "../../../Styles";

const CircleShortcutWrapper = styled(({ hovered, hoverBg, normalBg, hoverTextColor, normalTextColor, ...props }) => (
	<div {...props} />
))`
	.circle-shortcut {
		width: 150px;
		height: 150px;
		min-width: 150px;
		min-height: 150px;
		transition: 250ms;
		box-shadow: 0 0 15px ${({ hoverBg }) => hoverBg} !important;
		${({ hovered }) => (hovered ? "transform: scale(0.975);" : "")}
		background-color: ${({ hovered, hoverBg, normalBg }) => (hovered ? hoverBg : normalBg)} !important;
		-webkit-box-reflect: below 0em -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(rgba(255, 255, 255, 0.1)));
	}

	.circle-shortcut h3 {
		color: ${({ hovered, hoverTextColor, normalTextColor }) =>
			hovered ? hoverTextColor : normalTextColor} !important;
	}
`;

function CircleShortcut(props) {
	const { linkTo, text, normalColor: normalTextColor, hoverColor: hoverTextColor, normalBg, hoverBg } = props;

	const [isHovered, setHovered] = useState(false);

	return (
		<NoSelect style={{ display: "inline-block" }}>
			<CircleShortcutWrapper
				hoverBg={hoverBg}
				hoverTextColor={hoverTextColor}
				normalBg={normalBg}
				normalTextColor={normalTextColor}
				hovered={isHovered}
			>
				<Link to={linkTo} draggable={false}>
					<Segment
						className="circle-shortcut"
						circular
						onMouseEnter={() => setHovered(true)}
						onMouseOver={() => setHovered(true)}
						onMouseLeave={() => setHovered(false)}
						onTouchStart={() => setHovered(true)}
						onTouchEnd={() => setHovered(false)}
						onTouchCancel={() => setHovered(false)}
					>
						<Header as="h3" style={{ textTransform: "uppercase" }}>
							{text || ""}
						</Header>
					</Segment>
				</Link>
			</CircleShortcutWrapper>
		</NoSelect>
	);
}

const memoComponent = memo(CircleShortcut, (prev, next) => true);
export default memoComponent;
export { memoComponent as CircleShortcut };
