import { memo, useState } from "react";
import { useDispatch } from "react-redux";
import { Helmet } from "react-helmet";
import { Header, Grid, Divider } from "semantic-ui-react";
import { HightlightedText } from "./HightlightedText";
import { Body, NormalText } from "../../../Styles";
import { changeImagePopupSource } from "../../../../redux/action";

function Personality(props) {
	const [queryString, setQueryString] = useState("");

	const dispatch = useDispatch();

	const src = "ISTJ-A.png";

	return (
		<Body>
			<Helmet>
				<title>Who I am | {process.env.REACT_APP_TITLE}</title>
			</Helmet>

			<Header size="huge" style={{ cursor: "default" }}>
				Who I am
			</Header>
			<Divider section />

			<Grid stackable>
				<Grid.Column width={6}>
					<picture>
						<source
							type="image/webp"
							srcSet={encodeURIComponent(src.replace(/\.(jpg|png|gif)/, ".webp"))}
						/>
						<img
							src={`${src}${queryString}`}
							alt="ISTJ-A personality test result"
							draggable="false"
							className="ui bordered rounded image"
							onContextMenu={(e) => e.preventDefault()}
							onClick={() =>
								dispatch(
									changeImagePopupSource({
										images: [src],
										activeIndex: 0,
									})
								)
							}
							onError={() => setTimeout(() => setQueryString(`?${Math.random()}`), 1000)}
							style={{ cursor: "pointer" }}
						/>
					</picture>
				</Grid.Column>
				<Grid.Column width={10}>
					<NormalText>
						I am an{" "}
						<HightlightedText url="https://www.16personalities.com/istj-personality">
							ISTJ-A
						</HightlightedText>{" "}
						person. Logic means a lot to me!
						<br />
						<br />I am an{" "}
						<HightlightedText url="https://www.16personalities.com/articles/mind-introverted-vs-extraverted">
							introvert
						</HightlightedText>{" "}
						- I feel energetic and comfortable when work independently. I am self-reflective and studious.
						<br />
						<br />I am{" "}
						<HightlightedText url="https://www.16personalities.com/articles/energy-intuitive-vs-observant">
							sensing
						</HightlightedText>{" "}
						- I am pragmatic so I see things by looking at how to make them in reality. I often focus on
						details just like every bit of this website: fonts, colours and backgrounds.
						<br />
						<br />I am a{" "}
						<HightlightedText url="https://www.16personalities.com/articles/nature-thinking-vs-feeling">
							thinking
						</HightlightedText>{" "}
						person - I make decision based on truths and principles and by analyzing the pros and cons. As a
						result, I enjoy technical fields where logic is important to finding the solution.
						<br />
						<br />I am{" "}
						<HightlightedText url="https://www.16personalities.com/articles/tactics-judging-vs-prospecting">
							judging
						</HightlightedText>{" "}
						- I am task-oriented and I like to have things planned.
						<br />
						<br />I am{" "}
						<HightlightedText url="https://www.16personalities.com/articles/identity-assertive-vs-turbulent">
							assertive
						</HightlightedText>{" "}
						- I am confident and do not worry too much.
					</NormalText>
				</Grid.Column>
			</Grid>
		</Body>
	);
}

const memoComponent = memo(Personality, (prev, next) => true);
export default memoComponent;
export { memoComponent as Personality };
